/* src/custom.css */

/* Highlight colors */
mark[data-highlight-color="yellow"] {
  background-color: #fefcbf;
}
mark[data-highlight-color="red"] {
  background-color: #fecaca;
}
mark[data-highlight-color="blue"] {
  background-color: #bfdbfe;
}

/* Legend square classes */
.legend-yellow { background-color: #fefcbf; }
.legend-red    { background-color: #fecaca; }
.legend-blue   { background-color: #bfdbfe; }

/* Make sure headings and lists display */
.editor-content h1 {
  font-size: 1.5em;
  margin: 0.5em 0 0.25em;
}
.editor-content ul {
  list-style: disc;
  margin-left: 1.5em;
}
.editor-content ol {
  list-style: decimal;
  margin-left: 1.5em;
}
